import Hero from '../../Feature/Components/Hero';
import List from '../../Feature/Components/Lits';
import useProductsList from '../../Lists/Products';
import Trees from '../../Assets/trees.jpg';
import SmallList from '../../Feature/Components/SmallList';

function PlanedProducts() {
  const { Panels, Strips, Floors } = useProductsList();
  return (
    <>
      <Hero
        src={Trees}
        alt="Hero image Hyvlade produkter."
        title="Hyvlade Produkter"
        description="Massiva trgolv, lister och paneler i svenska lvtrslag  specialtillverkat efter dina nskeml"
      />
      <List
        title="Golv"
        description="Ett golv r inte bara ett golv  det r en mbel. Massiva trgolv i svenskt lvtr r unika: varje tilja har sin egen karaktr. Knslan av att g barfota p ett riktigt trgolv r ngot helt annat. Genom att vara med i hela frdlingsprocessen kan vi ursprungsgarantera ditt golv.Vra golv r bestllningsvara. Leveranstid meddelas vid offert."
        list={Floors}
      />
      <SmallList showAdditionalProducts />
      <List
        title="Paneler"
        description="Vi specialtillverkar paneler enligt kundens nskeml. Vi rekommenderar bastupanel i al, vilket ger en varm och gemytlig knsla och har liknande egenskaper som asp. Vi hjlper dig grna att frverkliga dina egna ider kring panel och utformning"
        list={Panels}
      />
      <List
        title="Lister"
        description="Vi erbjuder lister och foder i alla mjliga former och dimensioner. Frutom vra standardlister kan vi ven tillverka specialbestllda varianter helt enligt dina nskeml. Vid renovering av ldre hus kan vi reproducera gamla profiler  skicka grna en bit av den befintliga listen eller fodret till oss fr ett kostnadsfrslag"
        list={Strips}
      />
    </>
  );
}

export default PlanedProducts;
